.tab {
    overflow: hidden;
    background-color: #f1f1f1;
}

.tab-border {
    /* border: 1px solid #ccc; */
}

.tab button {
    float: left;
    border: none;
    outline: none;
    cursor: pointer;
    padding: 5px 16px;
    -webkit-transition: 0.3s;
    transition: 0.3s;
    font-size: 16px;
    margin: 2px;
}

.tab button:hover {
    background-color: lightsteelblue;
}

.tab button.active {
    background-color: darkgray;
}

.tabContent {
    padding: 6px 12px;
    border: 1px solid #ccc;
    border-top: none;
    /* width: 100%; */
}
.radio {
    display: block;
    position: relative;
    padding-left: 35px;
    padding-right: 10px;
    /* margin-bottom: 12px; */
    cursor: pointer;
    font-size: 1rem;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

/* Hide the browser's default radio button */
.radio input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
}

/* Create a custom radio button */
.checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: lightgray;
    border-radius: 50%;
}

/* On mouse-over, add a grey background color */
.radio:hover input~.checkmark {
    background-color: lightsteelblue;
}

/* When the radio button is checked, add a blue background */
.radio input:checked~.checkmark {
    background-color: #2196F3;
}

/* Create the indicator (the dot/circle - hidden when not checked) */
.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

/* Show the indicator (dot/circle) when checked */
.radio input:checked~.checkmark:after {
    display: block;
}

/* Style the indicator (dot/circle) */
.radio .checkmark:after {
    top: 9px;
    left: 9px;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: white;
}
